@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
/* Font Import */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  line-height: 1.5;
  background: #eeeeee;
  color: #333333;
}

.form-group,
.form-group label,
.form-group input {
  width: 100%;
  margin-bottom: 10px;
}

.form-group label {
  font-size: 0.8rem;
  font-weight: 500;
}

.form-group input {
  padding: 10px 20px;
  border: none;
  border-bottom: 3px solid transparent;
  background-color: #eeeeee;
  outline-width: 0;
  font-size: 1rem;
}

.form-group input:focus {
  border-bottom: 3px solid #ffb703;
}

.btn {
  padding: 10px 20px;
  cursor: pointer;
  width: 100%;
  font-size: 18px;
  border: none;
  border-radius: 2px;
}

.btn:hover {
  opacity: 0.8;
}

.btn-primary {
  font-weight: 700;
  background-color: #ffb703;
  color: #ffffff;
}

.error-message {
  width: 100%;
  display: inline-block;
  padding: 5px;
  background: red;
  color: #ffffff;
  text-align: center;
  margin: 5px 0;
}

.success-message {
  width: 100%;
  display: inline-block;
  padding: 5px;
  background: green;
  color: #ffffff;
  text-align: center;
  margin: 5px 0;
}

.data {
  max-width: 1200px;
  margin: 100px auto;
  padding: 0px 15px;
}

@media only screen and (max-width: 600px) {
  .data {
    max-width: 1200px;
    margin: 150px auto;
    padding: 0px 15px;
  }
}

nav {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 99;
  padding: 20px;
  background-color: #023047;
}

.nav-container {
  margin: auto;
  max-width: 1200px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
}

.nav-logo {
  text-decoration: none;
  color: #ffffff;
}

.nav-links {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
}

.nav-btn {
  padding: 8px 10px;
  cursor: pointer;
  width: 100%;
  font-size: 14px;
  font-weight: 700;
  border: none;
  border-radius: 2px;
  color: #023047;
  background-color: #fb8500;
}

@media only screen and (min-width: 600px) {
  .data-chart {
    width: 50%;
  }
}

.sensor-container {
  width: 100%;
  padding: 20px;
  border-radius: 7px;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.sensor-text {
  font-weight: 600;
  padding: 0px 10px;
}

.sensor-text-heading {
  margin-bottom: 10px;
}

.details-container {
  width: 100%;
  margin: 0px 0px 30px 0px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.patient-metadata-container {
  flex: 1 1;
  border-right: 1px solid #000;
  padding: 0px 5px;
}

.sensor-details-container {
  flex: 1 1;
  padding: 0px 5px 0px 20px;
}

@media only screen and (max-width: 600px) {
  .details-container {
    width: 100%;
    margin: 20px 0px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }

  .patient-metadata-container {
    border: none;
  }

  .sensor-details-container {
    padding: 0px 5px;
  }
}

.patient-metadata {
  margin-left: 20px;
}

.sensor-details {
  margin-left: 20px;
}

.indicator.online {
  background: rgb(140, 233, 0);
  display: inline-block;
  width: 0.8em;
  height: 0.8em;
  margin-left: 4px;
  border-radius: 50%;
  -webkit-animation: pulse-animation 2s infinite linear;
          animation: pulse-animation 2s infinite linear;
}

@-webkit-keyframes pulse-animation {
  0% {
    -webkit-transform: scale(1);
  }
  25% {
    -webkit-transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
  }
  75% {
    -webkit-transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1);
  }
}

@keyframes pulse-animation {
  0% {
    -webkit-transform: scale(1);
  }
  25% {
    -webkit-transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
  }
  75% {
    -webkit-transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1);
  }
}

.indicator.offline {
  background: #ff4136;
  display: inline-block;
  width: 0.8em;
  height: 0.8em;
  margin-left: 4px;
  border-radius: 50%;
}

@media only screen and (min-width: 600px) {
  .sensor-chart {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.login-screen {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-screen-form {
  width: 380px;
  margin: 20px;
  padding: 30px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  background: #ffffff;
}

.login-screen-title {
  text-align: center;
  margin-bottom: 15px;
}

.login-screen-subtext {
  font-size: 12px;
  display: block;
  margin: 5px 0;
}

